@media (min-width: 1200px) {
  .container {
    width: 1200px; } }

body * {
  font-family: "Cerebri Sans",Helvetica,Arial,sans-serif;
  font-size: 14px;
  font-weight: 300; }

header {
  background-image: url(/images/bg.png);
  background-size: cover;
  background-position: center; }
  header nav.fixed_scroll_menu {
    position: fixed;
    width: 100%;
    background-color: #ffffff;
    box-shadow: 0 1px 2px 0 #BDBDBD;
    z-index: 100;
    top: -80px;
    transition-property: top;
    transition-duration: 1.5s; }
    header nav.fixed_scroll_menu.fixed_header {
      top: 0; }
    header nav.fixed_scroll_menu .container .row {
      padding-top: 15px;
      padding-bottom: 15px; }
      header nav.fixed_scroll_menu .container .row .logo_menu ul.header-navigation li a {
        color: #040948; }
  header nav .row {
    padding-top: 30px;
    display: flex;
    align-items: center; }
    header nav .row .logo_menu {
      display: flex;
      flex: 1; }
      header nav .row .logo_menu .logo {
        margin-right: 36px; }
      header nav .row .logo_menu ul.header-navigation {
        list-style: none;
        padding: 0px;
        margin: 0px;
        display: flex; }
        header nav .row .logo_menu ul.header-navigation li {
          margin: 0px 32px 0px 0px; }
          header nav .row .logo_menu ul.header-navigation li a {
            text-decoration: none;
            color: #ffffff;
            font-size: 18px;
            line-height: 35px; }
    header nav .row .cta_button a {
      text-decoration: none;
      padding: 10px 34px;
      margin: 0px;
      border: 1px solid #403181;
      background-color: #403181;
      color: #ffffff;
      text-transform: uppercase;
      font-size: 14px;
      font-weight: 300; }
  header .blog_description .row h1 {
    color: #ffffff;
    margin-top: 92px;
    margin-bottom: 20px;
    line-height: 61px;
    max-width: 663px;
    font-size: 48px;
    font-weight: 500; }
  header .blog_description .row .short_description {
    color: #ffffff;
    margin-bottom: 114px;
    font-size: 20px; }

.container-fluid .row.main_content {
  margin: 0 1%;
  padding-top: 72px;
  display: flex;
  align-items: stretch; }
  .container-fluid .row.main_content .articles_list {
    display: flex;
    flex-wrap: wrap; }
    .container-fluid .row.main_content .articles_list .article_item {
      width: 50%;
      padding: 0px 25px;
      margin-bottom: 50px; }
      .container-fluid .row.main_content .articles_list .article_item .article_thumbnail img {
        width: 100%;
        height: auto; }
      .container-fluid .row.main_content .articles_list .article_item .article_params {
        margin-top: 16px;
        display: flex;
        justify-content: flex-start; }
        .container-fluid .row.main_content .articles_list .article_item .article_params span {
          line-height: 18px; }
        .container-fluid .row.main_content .articles_list .article_item .article_params .article_author {
          flex: 1; }
      .container-fluid .row.main_content .articles_list .article_item .article_title h2 {
        font-weight: 700;
        color: #040948;
        font-size: 20px;
        line-height: 28px;
        margin-top: 6px; }
      .container-fluid .row.main_content .articles_list .article_item .article_title .article_description {
        font-size: 16px;
        line-height: 22px; }
  .container-fluid .row.main_content .right_sidebar .move_part {
    position: relative;
    transition-property: top;
    transition-duration: 1.5s;
    top: 0;
    background-color: rgba(160, 162, 180, 0.5);
    padding: 25px; }
    .container-fluid .row.main_content .right_sidebar .move_part.fixed_bar {
      position: sticky;
      top: 147px; }
    .container-fluid .row.main_content .right_sidebar .move_part .sidebar_header {
      display: flex;
      align-items: center;
      margin-bottom: 27px; }
      .container-fluid .row.main_content .right_sidebar .move_part .sidebar_header .category_title {
        font-size: 24px;
        font-weight: 700;
        line-height: 30px;
        opacity: 0.8;
        color: #040948;
        flex: 1; }
    .container-fluid .row.main_content .right_sidebar .move_part ul.category_items_list {
      list-style: none;
      padding: 0px;
      margin: 0px; }
      .container-fluid .row.main_content .right_sidebar .move_part ul.category_items_list li.category_item a {
        font-size: 16px;
        font-weight: 700;
        line-height: 21px;
        opacity: 0.8;
        color: #040948;
        text-decoration: none;
        display: block;
        padding-left: 12px;
        margin-bottom: 4px;
        margin-top: 21px;
        border-bottom: 1px solid rgba(160, 162, 180, 0.2);
        transition-property: font-size, line-height, color, margin-bottom, margin-top,border-bottom;
        transition-duration: 0.2s; }
      .container-fluid .row.main_content .right_sidebar .move_part ul.category_items_list li.category_item.active a, .container-fluid .row.main_content .right_sidebar .move_part ul.category_items_list li.category_item:hover a {
        font-size: 30px;
        line-height: 38px;
        color: #40C4FF;
        margin-bottom: 2px;
        margin-top: 21px;
        border-bottom: 3px solid #40C4FF; }
